.main-nav {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 270px;
  padding: 20px;
  padding-bottom: 30px;
  padding-left: 30px;
  padding-top: 75px;
  background: $white;
  z-index: 2200;
  transition: all .4s;
  transform: translateX(110%);

  @include xl {
    position: static;
    width: auto;
    margin-left: auto;
    margin-right: 20px;
    padding: 0;
    transform: none;
    background: #ffffff;
    transition: none;
    display: none;
  }

  &--open {
    transform: translateX(0);
  }

  .btn-city {
    display: block;
    margin: auto;
    margin-bottom: 30px;
  }

  .os-content {
    display: flex;
    flex-direction: column;
    // height: calc(100vh - 185px);
    
  }
}

.main-nav__list,
.os-content {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 185px);
  // overflow-y: auto;

  @include xl {
    flex-direction: row;
  }
}



.main-nav__item {
  position: relative;
  margin-bottom: 8px;
  padding: 5px;
  padding-bottom: 8px;
  text-decoration: none;
  color: $black;
  font-size: 18px;
  line-height: 1.2em;
  transition: all .2s;
  font-weight: 700;
  

  @include xl {
    margin-bottom: 0;
    color: #404248;
    font-size: 16px;
  }

  // &:before {
  //   content: '';
  //   display: block;
  //   width: 100%;
  //   position: absolute;
  //   left: 0;
  //   bottom: 0;
  //   border-top: 1px solid $gray;
  // }

  &:hover {
    color: $color-second;
  }
}

.main-nav__close {
  position: absolute;
  top: 10px;
  right: 10px;
  // width:50px;
  // height:50px;
  width:40px;
  height:40px;
  margin: 0;
  border-radius:5px;
  cursor: pointer;
  background: #e6e6e6;

  @include xl {
    display: none;
  }

  &:hover {
    background:rgba(0,0,0,.7);
  }
}

.main-nav__close-inner {
  position:absolute;
  height:100%;
  width:100%;
  transform:rotate(45deg);

  span{
    display:block;
    background: $black;
    border-radius:2px;

    &:nth-child(1){
      height:80%;
      width:4px;
      position:absolute;
      top:10%;
      // left:24px;
      left: 43%;
      transition-delay:0s;
    }
    &:nth-child(2){
      width:80%;
      height:4px;
      position:absolute;
      left:10%;
      // top:24px;
      top: 44%;
      transition-delay:.25s;
    }
  }
}

.nav-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,.7);
  z-index: 2190;

  &--open {
    display: block;
    animation: myfadein ease .4s;

    @include xl {
      display: none;
    }
  }
}