// В этом файле должны быть стили для БЭМ-блока address, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.address {

  $block-name:                &; // #{$block-name}__element

  text-align: center;

  @include md {
    text-align: left;
  }
}

.address__title {
  max-width: 163px;
  margin: auto;
  margin-bottom: 14px;
  padding: 4px 11px;
  color: $white;
  background: $black;
  font-weight: 600;

  @include md {
    margin: 0;
    margin-bottom: 14px;
  }
}

.address__item {
  margin-bottom: 8px;
  color: $black;
  font-weight: 300;
  text-decoration: none;
}