// В этом файле должны быть стили для БЭМ-блока mainnav-contacts, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.mainnav-contacts {

  $block-name:                &; // #{$block-name}__element

  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;

  @include sm {
    margin: 0;
    align-items: flex-end;
  }
  
  @include md {
    flex-direction: row;
    align-items: center;
  }

  &__link {
    position: relative;
    padding-bottom: 3px;
    margin-bottom: 2px;

    @include md {
      margin-right: 50px;
      padding-bottom: 5px;
    }

    &::before {
      content: '';
      display: block;
      width: 100%;
      border-top: 2px solid $black;
      position: absolute;
      bottom: 0;
      left: 0;
      opacity: 0;
      transition: all .2s;
      transform: translateY(4px);

      @include md {
        transform: translateY(10px);
      }
    }

    &:hover {
      
      &::before {
        opacity: 1;
        transform: translateY(0);
      }
    }

    &:last-child {
      margin-right: 0;
    }
  }
}


