// В этом файле должны быть стили для БЭМ-блока mainnav-mobile, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.mainnav-desk {

  $block-name:                &; // #{$block-name}__element
  flex-grow: 1;
  display: none;

  @include xl {
    display: block;
  }

  &__list {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  &__item {
    position: relative;
    margin-right: 65px;
    text-align: center;

    &::before {
      content: '';
      display: block;
      width: 100%;
      border-top: 3px solid $black;
      position: absolute;
      bottom: 0;
      left: 0;
      opacity: 0;
      transform: translateY(21px);
      transition: all .2s;
    }
    &::after {
      content: '';
      display: none;
      width: 100%;
      border-top: 3px solid $white;
      position: absolute;
      left: 0;
      bottom: -52px;
      transition: all .2s;

      @include xxl {
        bottom: -78px;
      }
    }

    &:hover {
      font-weight: 700;

      &::before {
        transform: translateY(0);
        opacity: 1;
      }
    }

    .sub-menu-open &:hover {
      
      &::after {
        display: block;
      }
    }

    .header--small.sub-menu-open & {

      &::after {
        bottom: -21px;
      }
    }
  }

  &__link {
    display: inline-block;
    vertical-align: top;
    padding: 16px 5px;

    &::before {
      display: block;
      content: attr(aria-label);
      font-weight: bold;
      height: 0;
      overflow: hidden;
      visibility: hidden;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}

.mainnav-desk__item--active {
  font-weight: 700;

  &::before {
    transform: translateY(0);
    opacity: 1;
  }

  &::after {
    display: block;
  }
}

.mainnav-desk__item--current {
  font-weight: 700;

  &::before {
    transform: translateY(0);
    opacity: 1;
  }
}