// В этом файле должны быть стили для БЭМ-блока footer-copy, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.footer-copy {

  $block-name:                &; // #{$block-name}__element

  padding-top: 30px;
  padding-bottom: 80px;

  @include xxl {
    padding-bottom: 40px;
  }

  &__inner {
    @include container();
  }
  &__grid {
    @include row();

    flex-direction: column-reverse;
    align-items: center;

    @include md {
      flex-direction: row;
    }
  }
  &__item {
    @include col(md, 4);
    
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    padding: 0 15px;
    text-align: center;
  
    @include md {
      display: block;
      margin-bottom: 0;
      text-align: left;
    }

    &--policy {
      text-align: center !important;
    }
    &--soc {
      
      @include md {
        display: flex;
        justify-content: flex-end;
      }
    }
  }
}

