// В этом файле должны быть стили для БЭМ-блока our-project, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.our-project {

  $block-name:                &; // #{$block-name}__element

  display: flex;
  flex-direction: column;
  margin: auto;
  margin-bottom: 50px;

  @include md {
    max-width: initial;
    margin: 0;
  }

  &:hover {

    .news-item__pic {
      transform: scale(1.1);
      
      &::after {
        background: rgba(0, 0, 0, 0.4);
      }
    }
  }

  &__pic {
    position: relative;
    margin-bottom: 15px;
    font-size: 0;
    transition: all .4s;
    height: 225px;

    @include sm {
      margin-bottom: 20px;
    }

    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background: rgba(0, 0, 0, 0.6);
      transition: all .4s;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    margin-bottom: 10px;
    font-size: 20px;
    line-height: 1.2em;
    font-weight: 600;
    color: $white;

    @include sm {
      margin-bottom: 14px;
    }

    @include md {
      font-size: 24px;
    }
  }

  &__text {
    margin-bottom: 10px;
    font-weight: 300;
    color: $white;

    @include sm {
      margin-bottom: 20px;
    }
  }

  &__link {
    color: $white;
    text-decoration: underline;
  }

  &--soon {

    .our-project__pic {
      display: flex;
      align-items: center;
      justify-content: center;
      background: linear-gradient(111.38deg, #3D3D3D 4.35%, rgba(61, 61, 61, 0) 111.02%);

      &::after {
        display: none;
      }
    }
    .our-project__label {
      font-weight: 600;
      font-size: 36px;
      letter-spacing: 0.2em;
      color: #E3E3E3;
      text-transform: uppercase;
    }
  }
}
