// В этом файле должны быть стили для БЭМ-блока page-header, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page-header {
  background: $white;
  box-shadow: 0 0px 5px 0 $black;

  @include xl {
    box-shadow: none;
  }

  &--fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2180;

    @include xl {
      z-index: 999999;
    }
  }

  &--small {
    box-shadow: 0 0px 5px 0 $black;

    @include xl {
      box-shadow: 0 0px 5px 0 $black;
    }

    .sub-menu-open & {

      @include xl {
        box-shadow: none;
      }
    }

    .page-header__inner {

      @include xl {
        padding-top: 10px;
        padding-bottom: 10px;
      }
    }
    .logo {

      @include xl {
        width: 80px;
      }
    }
  }

  &__inner {
    @include container();

    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
    transition: all .2s;

    @include xl {
      justify-content: flex-start;
      padding-top: 30px;
      padding-bottom: 30px;
    }

    @include xxl {
      padding-top: 49px;
      padding-bottom: 49px;
    }
  }
}
