// В этом файле должны быть стили для БЭМ-блока our-cases, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.our-cases {

  $block-name:                &; // #{$block-name}__element

  padding: 30px 0;
  background: #f0f0f0;

  @include lg {
    padding: 60px 0;
  }
  @include xl {
    padding: 100px 0;
  }

  &__inner {
    @include container();
  }

  &__title {
    margin-bottom: 30px;
    font-size: 36px;

    @include md {
      margin-bottom: 60px;
      font-size: 48px;
    }
  }

  &__grid {
    

    @include sm {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 25px 20px;
    }
    @include lg {
      grid-template-columns: repeat(3, 1fr);
    }
    @include xl {
      gap: 40px 30px;
    }
  }

  &__btns {
    margin-top: 30px;
    display: flex;
    justify-content: center;

    @include lg {
      margin-top: 60px;
    }
  }

}
