// В этом файле должны быть стили для БЭМ-блока our-case, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.our-case {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 230px;
  margin-bottom: 20px;
  overflow: hidden;

  @include sm {
    min-height: 32vw;
    margin-bottom: 0;
  }
  @include md {
    min-height: 28vw;
  }
  @include lg {
    min-height: 21vw;
  }
  @include xl {
    padding: 30px;
  }
  @include rwd(1600) {
    padding: 50px 56px 54px;
  }
  @include rwd(1900) {
    min-height: 400px;
  }

  &:hover {

    .our-case__bg {
      transition: all 7s;
      transform: scale(1.2);

      &::after {
        background: rgba(0, 0, 0, 0.4);
      }
    }
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all 0.5s;

    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background: rgba(0, 0, 0, 0.6);
      transition: all .4s;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__name {
    position: relative;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 28px;
    color: $white;

    @include xl {
      font-size: 36px;
    }
  }

  &__info {
    position: relative;
    padding-top: 16px;
    font-weight: 300;
    color: $white;

    &::before {
      content: '';
      display: block;
      width: 130px;
      border-top: 1px solid $white;
      position: absolute;
      top: 0;
      left: 0;
    }

    span {
      margin-right: 26px;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}
