// В этом файле должны быть стили для БЭМ-блока btn, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.btn {
  box-sizing: border-box;
  display: inline-block;
  max-width: 100%;
  margin: 0;
  // border-radius: $border-radius;
  // border: 1px solid $gray-lighter;
  border: 0;
  padding: $field-padding-vertical  $field-padding-horizontal;
  vertical-align: middle;
  white-space: nowrap;
  text-overflow: ellipsis;
  user-select: none;
  text-align: center;
  // font-size: 1em;
  font-family: inherit;
  // line-height: $line-height;
  text-decoration: none;
  text-transform: none;
  color: $white;
  background-color: $color-main;
  background-image: none;
  cursor: pointer;
  overflow: hidden;
  transition: all .2s;

  &:hover,
  &:focus {
    text-decoration: none;
    color: $white;
    background-color: rgba(25, 25, 25, 0.6);
    // border-color: darken($gray-lighter, 12%);
  }

  // &:visited {
  //   color: $text-color;
  // }

  &:active,
  &:focus {
    @include field-focus;
  }

  &--disabled,
  &:disabled {
    opacity: $opacity;
    cursor: not-allowed;
    color: $text-color;
    background-color: $gray-lightest;
    // border-color: $gray-lighter;

    @at-root a.btn--disabled {
      pointer-events: none;
    }
  }

  &--hoverup {
    transition: all 0.3s ease-out;
    
    &:hover {
      transform: translateY(-2px) scale(1.005) translateZ(0);
      box-shadow: 0 6px 10px rgba(0, 0, 0, 0.11), 0 6px 9px rgba(47,47,47,0.76);
    }
  }

  &--big {
    background: #2b2a29;
    color: $white;
    font-size: 20px;
    padding: 13px 20px 18px;

    @include sm {
      font-size: 24px;
      padding: 13px 56px 18px;
    }
  }

  &--med {
    background: #2b2a29;
    color: $white;
    font-size: 20px;
    padding: 13px 20px 18px;

    @include sm {
      font-size: 24px;
      padding: 13px 26px 18px;
    }
  }

  // &__icon {
  //   display: inline-block;
  //   vertical-align: middle;
  // }



  // &--main {
  //   color: #fff;
  //   background-color: $color-main;
  //   // border: 1px solid $color-main;

