// В этом файле должны быть стили для БЭМ-блока news-item, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.news-item {

  $block-name:                &; // #{$block-name}__element

  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: auto;
  margin-bottom: 40px;

  @include sm {
    max-width: initial;
    margin: 0;
  }

  &:hover {

    .news-item__pic {
      
      &::after {
        opacity: 0;
      }
    }

  }

  &__pic {
    position: relative;
    margin-bottom: 10px;
    font-size: 0;

    @include sm {
      height: 42vw;
      margin-bottom: 20px;
    }

    @include lg {
      height: 21vw;
    }

    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background: rgba(0, 0, 0, 0.3);
      transition: all .4s;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    margin-bottom: 10px;
    font-size: 20px;
    line-height: 1.2em;
    font-weight: 600;

    @include sm {
      margin-bottom: 14px;
    }

    @include md {
      font-size: 24px;
    }
  }

  &__text {
    font-weight: 300;
  }

  &__btns {
    margin-top: 50px;
  }
}
