// В этом файле должны быть стили для БЭМ-блока clients-slider, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.clients-slider {

  $block-name:                &; // #{$block-name}__element

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 6px;

    @include sm {
      padding: 0 15px;
    }
  }
  &__pic {
    height: 160px;
    display: flex;
    justify-content: center;
    align-items: center;

    @include sm {
      margin-bottom: 30px;
    }

    @include md {
      height: 205px;
    }

    @include xl {
      height: 305px;
      margin-bottom: 0;
    }

    picture {
      height: 100%;
      display: flex;
      align-items: center;
    }

    img {
      width: auto;
      max-width: 100%;
      max-height: 100%;
    }
  }

  &__pic + &__pic {
    margin-bottom: 0;
  }

  
}
