// В этом файле должны быть стили для БЭМ-блока our-clients, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.our-clients {

  $block-name:                &; // #{$block-name}__element

  padding: 30px 0;

  @include lg {
    padding: 60px 0;
  }
  @include xl {
    padding: 100px 0;
  }

  &__inner {
    @include container();

    @include xl {
      width: 1170px;
    }
    @include xxl {
      width: 90vw;
    }
  }

  &__title {
    margin-bottom: 30px;
    font-size: 36px;

    @include md {
      margin-bottom: 60px;
      font-size: 48px;
    }
  }

  &__btns {
    display: flex;
    justify-content: center;
    margin-top: 35px;

    @include xl {
      margin-top: 15px;
    }
  }

  &__btn {
    position: relative;
    overflow: visible;
    
    &:before {
      content: "О ДА!";
      position: absolute;
      transition: 0.5s ease;
      left: 0;
      right: 0;
      margin: auto;
      top: -15px;
      font-size: 12px;
      font-weight: 600;
      opacity: 0;
      color: #2b2a29 !important;
    }
    
    &:hover {

      &:before {
        animation: omg-yes 1s;
      }
    }
  }
  
}
