// В этом файле должны быть стили для БЭМ-блока promo, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.promo {

  $block-name:                &; // #{$block-name}__element

  @include lg {
    height: calc(100vh - 87px);
  }
  @include xl {
    height: calc(100vh - 156px);
  }
  @include xxl {
    height: calc(100vh - 209px);
  }

  &__wrapper {
    @include container();
    padding-left: 0;
    padding-right: 0;
    display: flex;
    flex-direction: column;
    height: 100%;

    @include xl {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__inner {
    position: relative;
    flex-grow: 1;
    display: flex;
    align-items: center;
    min-height: 330px;
    padding-left: 15px;
    padding-right: 15px;

    @include xl {
      min-height: initial;
      padding: 0;
    }
  }

  &__shine {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__info {
    position: relative;
    margin-bottom: 40px;
    max-width: 1100px;

    @include sm {
      margin-left: 15px;
    }

    @include md {
      margin-left: 45px;
    }
    @include lg {
      display: flex;
      align-items: center;
    }
  }

  &__text {
    color: $white;
    max-width: 460px;
    margin-bottom: 30px;

    @include lg {
      margin-bottom: 0;
      margin-right: 30px;
    }
  }

  &__title {
    max-width: 480px;
    margin: 0;
    font-size: 36px;
    color: $white;
    padding-left: 15px;
    border-left: 5px solid $white;

    @include sm {
      padding-left: 26px;
      font-size: 52px;
      font-weight: 700;
    }
    @include xxl {
      font-size: 72px;
    }
  }

  &__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2rem 15px;

    @include sm {
      flex-direction: row;
      justify-content: space-between;
    }
    @include xl {
      padding: 2rem 0;
    }
  }
}
