// В этом файле должны быть стили для БЭМ-блока mainnav-desk-sub, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.mainnav-desk-sub {

  $block-name:                &; // #{$block-name}__element

  // display: none;
  // position: fixed;
  // width: 100%;
  // left: 0;
  // right: 0;
  // top: 100px;
  // background: $white;
  
  @include container();

  flex-grow: 1;
  

  @include xxl {
    
    max-width: 1410px;  
  }
  
  &__grid {
    @include row();

    display: none;
    padding-top: 20px;
  }
  &__col {
    display: flex;
    flex-direction: column;
    width: 20%;
    flex-basis: 20%;
    flex-shrink: 0;
    padding: 0 15px;

    [data-menu-tab=agency] & {
      @include col(lg, 3, $grid-columns, 80px);
    }
  }
  &__title {
    margin-bottom: 30px;
    padding-bottom: 13px;
    text-transform: uppercase;
    font-weight: 700;
    border-bottom: 1px solid $black;
  }
  &__item {
    margin-bottom: 8px;
    padding: 6px 3px;
    font-weight: 300;
    transition: none;

    &:hover {
      font-weight: 600;
    }
  }
  &__active {
    display: flex;
    animation: myfadein ease .2s;
  }
}
