// В этом файле должны быть стили для БЭМ-блока social, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.social {

  $block-name:                &; // #{$block-name}__

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;

  &__link {
    display: block;
    padding: 10px;
    border: 1px solid $white;
    
    &:hover {
      border-color: $black;
    }

    // #{$block-name}__link-wrap--fb & {
    //   background-color: $color-fb;
    // }

    // #{$block-name}__link-wrap--vk & {
    //   background-color: $color-vk;
    // }

    // #{$block-name}__link-wrap--tw & {
    //   background-color: $color-tw;
    // }

    // #{$block-name}__link-wrap--gh & {
    //   background-color: $color-gh;
    // }

    svg {
      display: block;
      width: 22px;
      height: 22px;
      fill: $black;
    }
  }

  &__link-wrap {
    margin-right: 6px;

    &--ytb {

      .social__link {
        padding: 8px;
      }

      svg {
        width: 26px;
        height: 26px;
      }
    }
  }

  &__text {
    @include hide-text;
  }
}
