.popup-modal {
  position: relative;
  z-index: 99999;
  font-family: -apple-system,BlinkMacSystemFont,avenir next,avenir,helvetica neue,helvetica,ubuntu,roboto,noto,segoe ui,arial,sans-serif;
}

.popup-modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-modal__container {
  background-color: #fff;
  padding: 30px;
  padding-top: 60px;
  max-width: 500px;
  max-height: 100vh;
  border-radius: 4px;
  // overflow-y: auto;
  box-sizing: border-box;

  .popup-modal--city & {
    max-width: 700px;
    width: 100%;
  }
}

// .popup-modal__header {
//   display: flex;
//   justify-content: space-between;
//   align-items: center;
// }

.popup-modal__title {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 600;
  font-size: 1.5rem;
  line-height: 1.25;
  box-sizing: border-box;
  text-align: center;
}

.popup-modal__close {
  position: absolute;
  top: 10px;
  right: 10px;
  background: transparent;
  border: 0;
  font-size: 30px;
  background: none;
  transition: all .2s;
  cursor: pointer;

  &:hover {
    background: #c6c6c6;
  }

  &:before { content: '\2715'; }
}

.popup-modal__header {
  margin-bottom: 2rem;

  .popup-modal__close {
    position: static;
  }
}

.popup-modal__content {
  margin-top: 2rem;
  margin-bottom: 2rem;
  line-height: 1.5;
  color: rgba(0,0,0,.8);
}

.popup-modal__btn {
  font-size: .875rem;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: .5rem;
  padding-bottom: .5rem;
  background-color: #e6e6e6;
  color: rgba(0,0,0,.8);
  border-radius: .25rem;
  border-style: none;
  border-width: 0;
  cursor: pointer;
  -webkit-appearance: button;
  text-transform: none;
  overflow: visible;
  line-height: 1.15;
  margin: 0;
  will-change: transform;
  -moz-osx-font-smoothing: grayscale;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  transition: -webkit-transform .25s ease-out;
  transition: transform .25s ease-out;
  transition: transform .25s ease-out,-webkit-transform .25s ease-out;
}

.popup-modal__btn:focus, .popup-modal__btn:hover {
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}

.popup-modal__btn-primary {
  background-color: #00449e;
  color: #fff;
}

.popup-modal--sub-menu.micromodal-slide.is-open {
  display: none;

  @include xl {
    display: block;
  }

  .popup-modal__overlay {
    display: block;
    // border-top: 1px solid $black;
    top: 156px;
    background: none;

    @include xxl {
      top: 209px;
    }
  }
   
  .popup-modal__container {
    display: flex;
    flex-direction: column;
    max-width: initial;
    width: 100%;
    height: 100%;
    border-radius: 0;
  }
  .popup-modal__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .popup-modal__close {
    top: 20px;
    right: 40px;
  }
}

.header--small {

  .popup-modal--sub-menu.micromodal-slide.is-open {

    .popup-modal__overlay {
      top: 96px;
    }
  }
}

/**************************\
  Demo Animation Style
\**************************/
@keyframes mmfadeIn {
    from { opacity: 0; }
      to { opacity: 1; }
}

@keyframes mmfadeOut {
    from { opacity: 1; }
      to { opacity: 0; }
}

@keyframes mmslideIn {
  from { transform: translateY(15%); }
    to { transform: translateY(0); }
}

@keyframes mmslideOut {
    from { transform: translateY(0); }
    to { transform: translateY(-10%); }
}

.micromodal-slide {
  display: none;
}

.micromodal-slide.is-open {
  display: block;
}

.micromodal-slide[aria-hidden="false"] .popup-modal__overlay {
  animation: mmfadeIn .3s cubic-bezier(0.0, 0.0, 0.2, 1);
}

.micromodal-slide[aria-hidden="false"] .popup-modal__container {
  animation: mmslideIn .3s cubic-bezier(0, 0, .2, 1);
}

.micromodal-slide[aria-hidden="true"] .popup-modal__overlay {
  animation: mmfadeOut .3s cubic-bezier(0.0, 0.0, 0.2, 1);
}

.micromodal-slide[aria-hidden="true"] .popup-modal__container {
  animation: mmslideOut .3s cubic-bezier(0, 0, .2, 1);
}

.micromodal-slide .popup-modal__container,
.micromodal-slide .popup-modal__overlay {
  will-change: transform;
}