// В этом файле должны быть стили для БЭМ-блока city, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.city {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  display: none;
  position: relative;
  flex-direction: column;
  width: 230px;

  @include xl {
    display: flex;
  }

  &--mobile {
    display: flex;
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid $black;
  }

  &__arrow {
    position: absolute;
    top: 7px;
    left: 0;
    transition: all .3s;
    width: 16px;
    height: 16px;
    background-image: url("data:image/svg+xml,%3Csvg width='19' height='20' viewBox='0 0 19 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8 18L16 10L8 2' stroke='%23C4C4C4' stroke-width='3'/%3E%3C/svg%3E%0A");
    background-repeat: no-repeat;
    background-size: contain;
  }

  &__item {
    padding-top: 3px;
    padding-bottom: 3px;
    border-bottom: 1px solid #c4c4c4;
    transition: all .3s;

    &:hover {
      font-weight: 600;
    }

    &:last-child {
      padding-bottom: 0;
      border-bottom: none;
    }

    &--active {
      padding-left: 24px;
      font-weight: 600;
    }
  }
}

