// В этом файле должны быть стили для БЭМ-блока footer-menu, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.footer-menu {

  $block-name:                &; // #{$block-name}__element
  @include col();
  @include col(md, 4);
  @include col(xl, 2);
  display: none;

  @include md {
    display: flex;
    flex-direction: column;
    margin-top: 35px;
  }
  @include xl {
    margin-top: 0;
  }
}

.footer-menu__title {
  margin-bottom: 18px;
  // font-size: 18px;
  font-weight: 600;
}

.footer-menu__item {
  margin-bottom: 10px;
  // font-size: 18px;
  text-decoration: none;
  font-weight: 300;

  &:hover {
    font-weight: 400;
  }
}