// В этом файле должны быть стили для БЭМ-блока our-projects, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.our-projects {

  $block-name:                &; // #{$block-name}__element

  padding: 40px 0;
  background: #1A1919;

  @include md {
    padding: 60px 0;
  }
  @include lg {
    padding: 100px 0;
  }

  &__inner {
    @include container();
  }

  &__title {
    margin-bottom: 30px;
    font-size: 36px;
    color: $white;

    @include md {
      margin-bottom: 60px;
      font-size: 48px;
    }
  }

  &__grid {
    
    @include md {
      display: grid;
      grid-template-columns: repeat(2,minmax(200px, 700px));
      gap: 40px;
    }
    @include xl {
      gap: 50px 80px;
    }
    @include rwd(1800) {
      gap: 50px 170px;
    }
  }

  &__btns {
    margin-top: 30px;

    @include lg {
      margin-top: 60px;
    }
  }
}
