// В этом файле должны быть стили для БЭМ-блока hamburger, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.hamburger {
  position: relative;
  display: block;
  width: 35px;
  height: 31px;
  margin-left: 25px;
  background: none;
  border: none;
  outline: none;

  @include md {
    margin-right: 0;
  }

  @include xl {
    display: none;
  }

  span {
    cursor: pointer;
    border-radius: 2px;
    height: 4px;
    width: 28px;
    background: #000;
    position: absolute;
    left: 0;
    top: 39%;
    display: block;

    &::before {
      cursor: pointer;
      border-radius: 2px;
      height: 4px;
      width: 35px;
      background: #000;
      position: absolute;
      top: -10px;
      display: block;
      content: '';
      border-radius: 2px;
    }
    &::after {
      cursor: pointer;
      border-radius: 2px;
      height: 4px;
      width: 35px;
      background: #000;
      position: absolute;
      bottom: -10px;
      display: block;
      content: '';
    }
  }
}