// В этом файле должны быть стили для БЭМ-блока our-services, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.our-services {

  $block-name:                &; // #{$block-name}__element

  background: linear-gradient(122.09deg, #191919 19.38%, #2E2E2E 103.5%);
  padding-top: 30px;

  @include lg {
    padding-top: 60px;
  }
  @include xl {
    padding-top: 100px;
  }

  &__inner {
    @include container();
  }

  &__title {
    margin-bottom: 30px;
    font-size: 36px;
    color: $white;

    @include md {
      margin-bottom: 60px;
      font-size: 48px;
    }
  }

  &__grid {
    
    max-width: 1200px;

    @include sm {
      display: grid;
      justify-content: space-around;
      grid-template-columns: repeat(2, 230px);
    }
    @include md {
      grid-template-columns: repeat(2, 300px);
    }
    @include lg {
      grid-template-columns: repeat(3, 300px);
      justify-content: space-between;
    }
    @include xl {
      margin-left: 150px;
      grid-template-columns: repeat(3, 320px);
    }
  }

  &__list {
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;
    max-width: 300px;

    @include rwd(360) {
      margin-left: 30px;
    }

    @include sm {
      max-width: initial;
      margin-left: 0;
    }

    @include xl {
      margin-bottom: 100px;
    }
  }

  &__sub-title {
    margin-bottom: 30px;
    padding-bottom: 15px;
    color: $white;
    font-size: 20px;
    text-transform: uppercase;
    border-bottom: 2px solid $white;
    font-weight: 700;

    @include xl {
      font-size: 24px;
    }
  }

  &__item {
    position: relative;
    margin-bottom: 10px;
    color: $white;
    font-weight: 300;
    font-size: 20px;

    @include xl {
      margin-bottom: 20px;
      font-size: 24px;
    }

    &:hover {
      color: $white;
      padding-left: 25px;

      &::before {
        opacity: 1;
        transform: translateX(0);
        transition: all .5s;
      }
    }

    &:focus {
      color: $white;
    }

    &::before {
      content: '';
      display: block;
      width: 17px;
      height: 23px;
      background-image: url("data:image/svg+xml,%3Csvg width='17' height='23' viewBox='0 0 17 23' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2 2L12.3261 11.6557L2 21' stroke='white' stroke-width='5'/%3E%3C/svg%3E");
      background-repeat: no-repeat;
      background-size: contain;
      position: absolute;
      top: 3px;
      left: 0;
      transform: translateX(-20px);
      opacity: 0;

      @include xl {
        top: 5px;
      }
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}
