// Сделаем viewport великим снова! (привет, IE10) http://timkadlec.com/2012/10/ie10-snap-mode-and-responsive-design/
@at-root {

  @-ms-viewport {
    width: device-width;
  }
}


/* stylelint-disable */
/* pt-sans-caption-regular - latin */
@font-face {
  font-family: 'PT Sans Caption';
  src: local('PT Sans Caption'), local('PTSans-Caption'),
       url('../fonts/pt-sans-caption-v12-latin-regular.woff2') format('woff2'),
       url('../fonts/pt-sans-caption-v12-latin-regular.woff') format('woff'),
       url('../fonts/pt-sans-caption-v12-latin-regular.ttf') format('truetype');
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
/* pt-sans-caption-700 - latin */
@font-face {
  font-family: 'PT Sans Caption';
  src: local('PT Sans Caption Bold'), local('PTSans-CaptionBold'),
       url('../fonts/pt-sans-caption-v12-latin-700.woff2') format('woff2'),
       url('../fonts/pt-sans-caption-v12-latin-700.woff') format('woff'),
       url('../fonts/pt-sans-caption-v12-latin-700.ttf') format('truetype');
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
/* stylelint-enable */

@keyframes fadeinblock {
  0% {
    display: block;
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
// animation: fadeinblock ease .4s;

@keyframes myfadein {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
// animation: myfadein ease .4s;

@keyframes slideInDown {
  from {
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}
// animation: slideInDown .6s both;

@keyframes slideInUp {
  from {
    transform: translate3d(0, 110%, 0);
    opacity: 0;
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}
// animation: slideInUp .6s both;

@keyframes iconjump {
  0%,
  to {
    transform: translateY(0);
  }

  20%,
  70% {
    transform: translateY(3px);
  }

  50% {
    transform: translateY(-1px);
  }
}
// animation: iconjump 1s ease-in-out;

@keyframes iconscrollup {
  0% {
    transform: translateY(0);
  }
  60% {
    transform: translateY(-110%);
  }
  65% {
    opacity: 0;
    transform: translateY(110%);
  }
  67% {
    opacity: 1;
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
  }
}
// animation: iconscrollup .4s cubic-bezier(0.83, 0.23, 0.48, 0.99) forwards;

@keyframes shadowpulse {
  0% {
    box-shadow: 0 0 0 0 $color-main;
  }
}
// animation: shadowpulse 1s;
// box-shadow: 0 0 0 2em rgba(#fff,0);

.sub-menu-open {

  .page-header {
    border-bottom: 1px solid #000;
  }
}

@keyframes omg-yes {
	50% {
		opacity: 1;
	}
	100% {
		top: -50px;
	}
}

.page {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: 100%;
  min-width: 320px;
  min-height: 100%;

  &__inner {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }

  &__content {
    flex-grow: 1;
    padding-top: 89px;
    
    @include xl {
      padding-top: 156px;
    }
    @include xxl {
      padding-top: 209px;
    }
  }

  &__footer-wrapper {
    flex-shrink: 0;
  }
}

