.slick-arrow {
  width: 50px;
  height: 50px;
  transform: none;
  z-index: 2;
  border: 3px solid #c4c4c4;
  border-radius: 50%;
  transition: all 0.2s;

  @include sm {
    width: 70px;
    height: 70px;
  }

  &:hover {
    border-color: #292929;

    svg {
      // fill: #292929;

      path {
        stroke: #292929;
      }
    }
  }

  &::before {
    display: none;
  }

  svg {
    width: 25px;
    height: auto;

    position: relative;
    font-size: 16px;
    // fill: #c4c4c4;
    transition: all .2s;

    @include sm {
      width: 38px;
    }

    path {
      transition: all .2s;
      stroke: #c4c4c4;
    }
  }
}

.slick-prev {
  top: -72px;
  right: 60px;
  left: initial;

  @include xl {
    top: 50%;
    left: -15px;
    transform: translateY(-50%);
  }

  svg {
    margin-left: 6px;

    @include sm {
      margin-left: 11px;
    }
  }
}
.slick-next {
  top: -72px;
  right: 0px;

  @include xl {
    top: 50%;
    right: -15px;
    transform: translateY(-50%);
  }

  svg {
    margin-right: 6px;

    @include sm {
      margin-right: 11px;
    }
  }
}

.policy {
  margin-bottom: 15px;
  color: $black;
  text-decoration: underline;

  @include md {
    margin-bottom: 0;
  }
}

