// В этом файле должны быть стили для БЭМ-блока page-footer, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page-footer {
  padding-top: 50px;
  padding-bottom: 30px;

  // &__row {
  //   display: grid;
  //   grid-template-columns: repeat(3, 1fr);
  //   gap: 16px;
  //   grid-auto-flow: row;
  // }
  &__inner {
    @include container();
  }
  &__grid {
    @include row();// это прямой родитель сетки, margin по умолчанию (из переменной)
  }
  &__logo {
    @include col(sm, 6);
    @include col(md, 4);
    @include col(xl, 2);

    margin-bottom: 35px;
    padding: 0 15px;
    text-align: center;

    @include sm {
      margin-bottom: 0;
      text-align: left;
    }

    img {
      max-width: 100%;
      width: 116px;
    }
  }
  &__btns {
    @include col(sm, 6);
    @include col(md, 4);
    @include col(xl, 2);

    margin-bottom: 45px;
    padding: 0 15px;
    text-align: center;

    @include sm {
      align-self: center;
      text-align: left;
    }

    @include md {
      margin-top: 35px;
      margin-bottom: 0;
      align-self: flex-start;
    }
    @include xl {
      margin-top: 20px;
    }
  }
  .btn {
    max-width: 261px;
    width: 100%;
  }

  .address {
    @include col(sm, 6);
    @include col(md, 4);
    @include col(xl, 2);

    margin-top: 30px;
    padding: 0 15px;
    order: 1;

    @include md {
      margin-top: 0;
      order: 0;
    }
  }
}
